.synopsis {
    width: 100%;
    height: 100%;
}
.synopsis_bg {
    width: 100%;
    height: 100%;
}
.synopsis_info {
    height: 100px;
    position: absolute;
    left: 50%;
    top: 50%;
    margin: auto;
    text-align: center;
    transform: translate(-50%,-50%);
}
.synopsis_info h2 {
    font-size: 3.75vw;
}
.synopsis_info p {
    font-size: 1.25vw;
    margin: 20px;
}
