.characters{
    width: 100%;
    height: 100%;
}
.swiper-prev, .swiper-next{
    width: 28px;
    height: 32px;
    position: absolute;
    top: 50%;
    font-size: 0;
    cursor: pointer;
    z-index: 4;
    transform: translateY(-50%);
}
.swiper-prev svg, .swiper-next svg{
    font-size: 48px;
}
.swiper-prev{
    left: 120px;
}
.swiper-next{
    right: 120px;
}

.char_bg{
    width: 100%;
    height: 100%;
    background-size: cover;
}
.char_info{
    height: 140px;
    position: absolute;
    top: 0;
    bottom: 0;
    margin: auto;
    right: 274px;
    text-align: right;
}
.char_info{
    font-size: 72px;
}
.char_info h5{
    margin-top: 20px;
    font-size: 36px;
}

.swiper-slide {
    opacity: 0 !important;
}

.swiper-slide.swiper-slide-active {
opacity: 1 !important;
}