.btn_box{
    width: 380px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto;
    position: fixed;
    left: 0;
    bottom: 64px;
    z-index: 4;
    right: 0;
}
button{
    color: #fff;
    font-size: 0;
    cursor: pointer;
}
/* 이전, 다음 버튼 */
.prev svg,
.next svg{
    width: 16px;
    font-size: 28px;
}
/* 음소거 */
.mute{
    font-size: 0;
    margin-right: 20px;
}
.mute svg{
    display: inline-block;
    margin-top: 2px;
    font-size: 32px;
}

/* Channel */
.btn_box h2{
    font-size: 48px;
    margin: 0 24px 0 20px;
}

/* 유튜브 영상 */
#player{
    width: 100%;
    height: 100%;
    opacity: 0.4;
    position: fixed;
    left: 0;
    top: 0;
    z-index: 1;
}
#player iframe {
    width: 115%;
    height: 115%;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
}